.dropdown-container .dropdown__selected-item .text{white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.dropdown-container{position:relative;display:block;z-index:5;color:var(--dropdown-text-color)}.dropdown-container .dropdown__option-list-item,.dropdown-container .dropdown__selected-item{width:max-content;min-width:12rem;max-width:22rem;user-select:none}.dropdown-container .dropdown__selected-item{display:inline-flex;align-items:flex-start;justify-content:flex-start;border:1px solid var(--divider-color);background-color:var(--dropdown-bg-color);padding:1rem .8rem}.dropdown-container .dropdown__selected-item .text{display:inline-block;font-size:var(--size-0);margin-top:.2rem;margin-right:1.8rem;flex-grow:1;text-align:left}.dropdown-container .dropdown__selected-item .selected-item__arrow{display:inline-block;flex-shrink:0;font-size:var(--size-3);transition:transform .2s ease-out}.dropdown-container .dropdown__selected-item:hover,.dropdown-container .dropdown__selected-item:focus{background-color:var(--dropdown-bg-color_hover)}.dropdown-container .dropdown__selected-item:hover .text,.dropdown-container .dropdown__selected-item:focus .text{font-weight:600}.dropdown-container .dropdown__option-list{position:absolute;display:none;left:0;bottom:-0.4rem;transform:translateY(100%);background-color:var(--dropdown-bg-color);border:1px solid var(--divider-color);box-shadow:3px 3px 0px 0px rgba(0,0,0,.15)}.dropdown-container .dropdown__option-list-item{padding:1rem .8rem;font-size:var(--size-0);cursor:pointer}.dropdown-container .dropdown__option-list-item:not(:last-child){border-bottom:1px solid var(--divider-color)}.dropdown-container .dropdown__option-list-item:hover{background-color:var(--dropdown-bg-color_hover)}.dropdown-container.is-expanded .dropdown__selected-item{background-color:var(--dropdown-bg-color_hover)}.dropdown-container.is-expanded .dropdown__selected-item .text{font-weight:600}.dropdown-container.is-expanded .dropdown__selected-item .selected-item__