.checkbox-container{position:relative;display:inline-flex;align-items:flex-start;justify-content:flex-start;width:max-content;color:var(--checkbox-text-color);margin-bottom:.8rem}.checkbox-container .checkbox__box{position:relative;display:inline-block;width:2rem;height:2rem;overflow:hidden;margin-right:1.2rem;border:1px solid var(--checkbox-text-color)}.checkbox-container .checkbox__box .mark{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:65%;height:65%;display:block;background-color:var(--checkbox-mark-color);opacity:0;transition:opacity .1s ease-out}.checkbox-container .checkbox__box .checkbox__input{position:absolute;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,0);cursor:pointer}.checkbox-container .checkbox__label{display:inline-block;cursor:pointer}.checkbox-container .checkbox__label:hover{font-weight:600}.checkbox-container.is-on .mark{opacity:1}.checkbox-container.is-on .checkbox__label{font-weight:600}